<template>
  <div class="dropdown">
    <button @mouseenter="Dropdown" @mouseleave = "closeDropdown" class="dropdown-button">
      <div class = "selected-content">
        {{ selectedOption }}
      </div>
      <div class = "icon"><i class="fi fi-rr-angle-small-down"></i></div>
    </button>
    <ul v-show="isOpen" class="dropdown-list" @mouseenter="openDropdown" @mouseleave="closeDropdown">
      <li v-for="(option, index) in options" :key="index" @click="selectOption(option)">
        {{ option.work_name }}
      </li>
    </ul>
  </div>
</template>



<script>
export default {
  props: {
    options: Array,
   initialWorkName: null,
  },

  mounted() {
    this.selectedOption = this.initialWorkName ? this.initialWorkName : " ";
  },
  data() {
    return {
      isOpen: false,
      selectedOption: '',
    };
  },
  methods: {
    openDropdown() {
      this.isOpen = true;
    },
    closeDropdown() {
      this.isOpen = false;
    },
    selectOption(option) {
      this.selectedOption = option.work_name;
      //   workId   
      this.$emit('selected', option.work_id);
      this.isOpen = false;
    },
    Dropdown() {
      this.isOpen = !this.isOpen;
    }
  },
};
</script>

