<script>
import { axiosInstance } from '@/axios';

import BoardDropdown from './components/BoardDropdown.vue';
import WritePage from './WritePage.vue';
import Loader from '../../components/Loader.vue';
import bgColors from '../../../public/color';
import { useStore } from 'vuex';
import ProfessorView from './HomeViewProfessor.vue';

export default {
  props: ['teamId'], // props    

  data() {
    return {
      boardList: [],
      workList: [],
      memberList: [],
      mergeList:[],
      totalList:[],
      currentPage: 0, //   
      visibleBoardData: [], //    
      feedbackTrueItems: [],  //  true   
      feedbackFalseItems: [], //  false   
      myfeedbackItems: [] ,//     
      totalPages: 0, //   
      falsePages:0,
      truePages:0,
      mywritePages:0,
      feedbackTrueItemCount:0,
      feedbackFalseItemCount:0,
      visiblePagesStart: 1, //      
      visiblePagesEnd: 3,   //      
      dropdownOptions: ['Option 1', 'Option 2', 'Option 3'],
      itemsPerPage: 7, //      
      loading: true, //    true 
      selectedIndex: null, //    
      colors: {}, //  bg 
      i: 0, //  
      bgColors,
      members: {},
      works : {},
      role: "",
    };
  },
  components: {
    BoardDropdown,
    WritePage,
    Loader,
    ProfessorView
  },
  mounted() {
    this.fetchBoardList(this.teamId);
  },
  computed: {
    visiblePageRange0() {
      const maxVisiblePages = 3;
      const middlePage = Math.floor((this.visiblePagesStart + this.visiblePagesEnd) / 2);
      const startPage = Math.max(1, middlePage - Math.floor(maxVisiblePages / 2));
      const endPage = Math.min(this.totalPages, startPage + maxVisiblePages - 1);

      return Array.from({ length: endPage - startPage + 1 }, (_, i) => startPage + i);
    },
    visiblePageRange1() {
      const maxVisiblePages = 3;
      const middlePage = Math.floor((this.visiblePagesStart + this.visiblePagesEnd) / 2);
      const startPage = Math.max(1, middlePage - Math.floor(maxVisiblePages / 2));
      const endPage = Math.min(this.truePages, startPage + maxVisiblePages - 1);

      return Array.from({ length: endPage - startPage + 1 }, (_, i) => startPage + i);
    },
    visiblePageRange2() {
      const maxVisiblePages = 3;
      const middlePage = Math.floor((this.visiblePagesStart + this.visiblePagesEnd) / 2);
      const startPage = Math.max(1, middlePage - Math.floor(maxVisiblePages / 2));
      const endPage = Math.min(this.falsePages, startPage + maxVisiblePages - 1);

      return Array.from({ length: endPage - startPage + 1 }, (_, i) => startPage + i);
    },
    visiblePageRange3() {
      const maxVisiblePages = 3;
      const middlePage = Math.floor((this.visiblePagesStart + this.visiblePagesEnd) / 2);
      const startPage = Math.max(1, middlePage - Math.floor(maxVisiblePages / 2));
      const endPage = Math.min(this.mywritePages, startPage + maxVisiblePages - 1);

      return Array.from({ length: endPage - startPage + 1 }, (_, i) => startPage + i);
    },
    feedbackCompletedCount() {
      //board.feedbackYn===4      4
      //board.feedbackYn===1    ()
      return this.boardList.filter(board => (board.feedbackYn===1||board.feedbackYn===2)).length;
    },
    feedbackIncompleteCount() {

      //        
      //board.feedbackYn===0      
      //board.feedbackYn===2      ()
      return this.boardList.filter((board => (board.feedbackYn===0))).length;
    },
    myBoardWriteCount() {
      //board.feedbackYn===3     
      return this.boardList.filter((board => (board.feedbackYn===3))).length;
    },
  },
  watch: {
    currentPage() {
      this.updateVisibleBoardData(); //      
    },
    boardList() {
      this.updateVisibleBoardData(); // boardList     
    },

      selectedIndex(newValue, oldValue) {
        if (newValue !== oldValue) {
          // selectedIndex   currentPage 0 .
          this.currentPage = 0;
        }
      },
  },
  methods: {
    updateSelectedIndex(index) {
      this.selectedIndex = index;
    },
    handleDropdownSelect(index) {
      // BoardDropdown     
      this.selectedIndex = index;
      this.currentPage = 0;
    },

    formatDateFromArray(dateArray) {
      //  , ,   
      const [year, month, day] = dateArray.slice(0, 3);

      //       0    .
      const formattedMonth = month.toString().padStart(2, '0');
      const formattedDay = day.toString().padStart(2, '0');

      // YYYY-MM-DD    
      const formattedDate = `${year}-${formattedMonth}-${formattedDay}`;

      return formattedDate;
    },


    async fetchBoardList(teamId) {
      try {
        const store = useStore();
        this.role = store.state.userStore.role;
        const userId = store.state.userStore.user_id;
        
        if (this.role == "student"){
          const url = `/board-service/board/list/${userId}/${teamId}`
          const response = await axiosInstance.get(url);

          //    boardList 
          this.boardList = response.data.content.reverse();
          console.log(response.data);
          this.workList=response.data.work;
          this.memberList=response.data.member;

          for (const member of this.memberList){
            this.members[member.id] = member;
          }
          for (const work of this.workList){
            this.works[work.workId] = work;
          }


          // boardId   
          this.totalList.sort((a, b) => b.boardId - a.boardId);
          this.feedbackTrueItemCount = this.feedbackTrueItems.length;
          this.feedbackFalseItemCount = this.feedbackFalseItems.length;
          //    
          this.totalPages = Math.ceil(this.boardList.length / this.itemsPerPage);
          //  visibleBoardData 
          this.falsePages=Math.ceil(this.feedbackIncompleteCount/this.itemsPerPage);
          this.truePages=Math.ceil(this.feedbackCompletedCount/this.itemsPerPage);
          this.mywritePages=Math.ceil(this.myBoardWriteCount/this.itemsPerPage);
          this.updateVisibleBoardData();
        }

      } catch (error) {
        console.error('Error fetching board list:', error);
      }finally {
        this.loading = false; //   false 
      }
    },

    updateVisibleBoardData() {
      const startIndex = this.currentPage * this.itemsPerPage;
      const endIndex = startIndex + this.itemsPerPage;
      ////////
      this.visibleBoardData = this.boardList.slice(startIndex, endIndex);
      //  true  false  
      this.feedbackTrueItems = this.boardList.filter(board => board.feedbackYn === 1||board.feedbackYn === 2).slice(startIndex, endIndex);
      this.feedbackFalseItems = this.boardList.filter(board => (board.feedbackYn===0)).slice(startIndex, endIndex);
      this.myfeedbackItems = this.boardList.filter(board => (board.feedbackYn ===3)).slice(startIndex, endIndex);
    },
    goToPage(pageNumber) {
      this.currentPage = pageNumber;
      this.updateVisibleBoardData(); //      
      // :      
      const maxVisiblePages = 3;
      const middlePage = Math.floor((this.currentPage + this.visiblePagesEnd) / 2);
      const startPage = Math.max(1, middlePage - Math.floor(maxVisiblePages / 2));
      this.visiblePagesStart = startPage;
      this.visiblePagesEnd = startPage + maxVisiblePages - 1;
    },
    showPreviousPages() {
      if (this.visiblePagesStart > 1) {
        this.visiblePagesStart -= 3;
        this.visiblePagesEnd -= 3;
      }
    },
    showNextPages() {
      if (this.visiblePagesEnd < this.totalPages) {
        this.visiblePagesStart += 3;
        this.visiblePagesEnd += 3;
      }
    },
    goToWritePage() {
      this.$router.push({ name: 'WritePage' }); // WritePage name   
    },
    getBackgroundColor(workName) {
      // work_id    
      if (!this.colors[workName]) {
        // work_id        
        this.colors[workName] = this.bgColors[this.i];
        if (this.i >= this.bgColors.length-1){
          this.i = 0;
        } else{
          this.i = this.i + 1;
        }
      }
      return this.colors[workName];
    },
  },
};
</script>

